
import EncounterMixinVue from '@/views/EncounterMixin.vue'
import { FieldType } from "@/components/Forms/BaseFormElements"
import { defineComponent } from 'vue'
import { Field, Option } from '@/components/Forms/FieldInterface'
import { IonPage } from "@ionic/vue"
import { HtsReferralAppointmentService } from "@/apps/HTS/services/hts_referral_appointment_service"
import { getFacilities } from '@/utils/HisFormHelpers/LocationFieldOptions'
import dayjs from 'dayjs'
import { toDate } from '@/utils/Strs'
import { Offline } from "@/composables/offline"
import { ObservationOffline } from '@/interfaces/Sync'

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as any,
    pregnancyRetest: false as boolean,
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsReferralAppointmentService(this.patientID, this.providerID)
          this.fields = [
            this.referralField(),
            this.referralLocationField(),
            this.appointmentDataField()
          ]
        }
      },
      immediate: true
    }
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
      })
    },
    referralLocationField(): Field {
      return {
        id: 'referral_location',
        helpText: 'Intended site for retesting',
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: ({ label }: Option) => ({
          obs: this.service.buildValueText('Referral location', label)
        }),
        defaultValue: () => HtsReferralAppointmentService.getLocationName(),
        condition: (f: any) => f.re_test.value != 'Not done',
        options: (_: any, filter = '') => getFacilities(filter),
        config: {
          footerBtns: [
            {
              name: "Here",
              slot: "end",
              onClickComponentEvents: {
                setValue: () => HtsReferralAppointmentService.getLocationName()
              },
              onClick: () => null
            }
          ],
          showKeyboard: true,
          isFilterDataViaApi: true
        }
      }
    },
    referralField(): Field {
      let reasonForRetest: any = null
      return {
        id: "re_test",
        helpText: 'Referral for Re-Testing',
        type: FieldType.TT_TEXT_BANNER,
        init: async () => {
          let hivStatus = ''
          let riskCategory = ''
          let pregnancyStatus = ''
          if (this.goOffline()) {
            const data = Offline().findByID(this.offlineID)
            if (!data || !Array.isArray(data.observationParams)) 
              return false
            const obs = (data.observationParams||[]).find(
              (d: ObservationOffline) => d.encounterName === 'testing'
            )?.meta
            hivStatus = obs?.hiv_result?.label || 'N/A'
            riskCategory = obs?.client_risk_category?.label || 'N/A'
            pregnancyStatus = (data.observationParams||[]).find(
              (d: ObservationOffline) => d.encounterName === 'pregnancy status'
            )?.meta?.hts_female_pregnant?.value
          } else {
            const service = new HtsReferralAppointmentService(this.patientID, this.providerID)
            riskCategory = (await service.getFirstValueCoded("client risk category")) || 'N/A'
            hivStatus = (await service.getFirstValueCoded("HIV Status")) || 'N/A'
            pregnancyStatus = (await service.getFirstValueCoded("Pregnancy status")) || 'N/A'
          }
          if (/Negative/i.test(hivStatus) && pregnancyStatus === "BREASTFEEDING") {
            this.pregnancyRetest = true
            reasonForRetest = { label: "Book a Re-Test for Female Breastfeeding Client", value: 'Re-test'}
          } else if (/Negative/i.test(hivStatus) && pregnancyStatus == 'Pregnant woman') {
            this.pregnancyRetest = true
            reasonForRetest = {label: "Book a Re-Test for Female Pregnant Client", value: 'Re-test'}
          } else if (/Positive Initial Professional/i.test(hivStatus)) {
            reasonForRetest = { label: "Confirmatory test is needed for Positive result", value: 'Confirmatory HIV test' }
          } else if (/high risk|High risk event|on-going/i.test(`${riskCategory}`)) {
            reasonForRetest = { label: `Retesting is required due to "${riskCategory}"`, value: 'Re-test' }
          } else {
            reasonForRetest = { label: 'No re-test required', value: 'Not done' }
          }
          return true
        },
        defaultValue: () => this.toOption(reasonForRetest.value),
        condition: () => reasonForRetest != null,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded('Referral for Re-Testing', v.value)
          }
        },
        options: () => {
          return [
            this.toOption(reasonForRetest.label)
          ]
        }
      }
    },
    appointmentDataField(): Field {
      let maxDate = ''
      let nextAppointmentDate = ''
      return {
        id: "set_appointment",
        helpText: "Select Appointment Date for Re-testing",
        type: FieldType.TT_DATE_PICKER,
        init: async () => {
          nextAppointmentDate = this.service.date
          if (this.pregnancyRetest) {
            maxDate = dayjs(nextAppointmentDate).add(365, 'day').format('YYYY-MM-DD')
          } else {
            try {
              const res = await this.service.nextAppointment()
              if (res) nextAppointmentDate = res
              maxDate = dayjs(nextAppointmentDate).add(15, 'day').format('YYYY-MM-DD')
            } catch (e) {
              // Returns 404 not appointment date is available
            }
          }
          return true
        },
        isRequired: () => true,
        condition: (f: any) => f.referral_location.value,
        defaultValue: () => nextAppointmentDate,
        finalComputedValue: (d: Option) => {
          return {
            obs: this.service.buildValueDate('Appointment date', d.value)
          }
        },
        config: {
          minDate: () => nextAppointmentDate,
          maxDate: () => maxDate,
          infoItems: (date: string) => {
            return [
              {
                label: 'System set appointment',
                value: toDate(nextAppointmentDate)
              },
              {
                label: 'User set appointment',
                value: toDate(date)
              }
            ]
          },
          hiddenFooterBtns: [
            'Clear'
          ]
        }
      }
    }
  }
})
